<template>
  <div>

    <form>
      <div class="form-group row">
        <label class="col-sm-1 col-form-label">Metode</label>
        <select class="custom-select col-sm-1">
          <option>Buat Kartu Parkir</option>
        </select>&nbsp;&nbsp;
        <button class="btn btn-primary mb-1" @click="processSelectedJukir()">Prosess</button>
      </div>
    </form>

    <vue-good-table
      @on-selected-rows-change="selectionChanged"
      :columns="columns"
      :rows="data"
      :pagination-options="{
    enabled: true,
    mode: 'pages',
    position: 'bottom',
    dropdownAllowAll: true,}"
      :search-options="{
    enabled: true,
    placeholder: 'Cari Jukir ',}"
      :selectOptions="{
    enabled: true,
    selectOnCheckboxOnly: true,
    selectionText: 'rows selected',
    clearSelectionText: 'clear',}">

      <template slot="table-row" slot-scope="props">
        <span v-if="props.column.field == 'action'">
          <b-button block variant="primary" @click="infoClicked(props.row)">Info</b-button>
          <b-button block variant="success" @click="updateClicked(props.row)">Update</b-button>
        </span>
        <span v-else-if="props.column.field == 'link_foto'">
          <img :src="'http://' + props.formattedRow[props.column.field]" class="thumbailTablePhoto"/>
        </span>
        <span v-else>
        {{props.formattedRow[props.column.field]}}
        </span>
      </template>
    </vue-good-table>

    <p class="error-msg">{{ errorMsg }}</p>

  </div>
</template>

<script>
  import {EventBus} from '../../_auth/utils/eventbus'
  import {mapState} from "vuex";

  export default {
    name: 'table-jukir',
    computed: mapState({
      selected_jukir: state => state.selected_jukir
    }),

    data() {
      return {
        columns: [
          {
            label: 'ID Jukir',
            field: 'id',
            thClass: 'text-center',
          },
          {
            label: 'Foto',
            field: 'link_foto',
            html: 'true',
            tdClass: 'text-center',
            thClass: 'text-center',
            width: '70px'
          },
          // {
          //   label: 'ID/Nama',
          //   field: 'id_and_name',
          //   thClass: 'text-center',
          // },
          {
            label: 'Nama',
            field: 'nm',
            thClass: 'text-center',
          },
          {
            label: 'Jalan',
            field: 'jalan',
            thClass: 'text-center',
          },
          {
            label: 'Action',
            field: 'action',
            thClass: 'text-center',
          },
        ],
        data: [],
        selected_jukir1: [],
        errorMsg: '',
      }
    },

    beforeMount: function () {
      this.getData();
    },

    mounted() {
      EventBus.$on('success', (msg) => {
        this.data = msg;
        this.errorMsg = "";
      });
      EventBus.$on('fail', (msg) => {
        this.data = [];
        this.errorMsg = msg;
      })
    },

    methods: {
      getData() {
        this.$store.dispatch('find_info_jukir_by_range', {})
          .then(() => this.$router.push('/table-jukir'))
      },

      updateClicked(param) {
        this.$router.push({
          path: '/detail/detail-jukir',
          query: {id: param.id}
        });
      },
      infoClicked(param) {
        this.$router.push({
          path: '/info/info-jukir',
          query: {id: param.id}
        });
      },

      selectionChanged(params) {
        this.selected_jukir1 = params.selectedRows;
      },

      processSelectedJukir() {
        this.$store.dispatch("setListJukir", this.selected_jukir1);
        this.$router.push({
          path: '/jukir-card-semarang'
        });
      }
    },

    beforeDestroy() {
      EventBus.$off('success');
      EventBus.$off('fail');
    },
  };
</script>
